import {
		mapState,  
	    mapMutations  
	} from 'vuex'; 
	
export default {
	onLoad(){
		uni.request({
		    url: this.global_baseUrl + '/news/',
		    data: {
				
		    },
		    success: (res) => {
				this.newsList = res.data.slice(0,10);
		    }
		});
		uni.request({
		    url: this.global_baseUrl + '/swipePicture/',
		    data: {
				
		    },
		    success: (res) => {
				// console.log(res.data);
				this.swiperList = res.data;
		    }
		});
		setTimeout(function () {
		    uni.hideLoading();
		}, 1000);
	},
	onShow(){
		var that = this;
		that.checkLocalInfo();
	},
	methods:{
		checkLocalInfo() {
			var that = this;
			if(that.token == null){
				setTimeout(() => {
					uni.showModal({
						title: '提示',
						content: '您还未登录，登录体验更多功能',
						cancelText: '先逛逛',
						confirmText: '去登录',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '/pages/login/login'
								});
							} else if (res.cancel) {}
						}
					});
				}, 800)
				uni.hideLoading();
			} else {
				uni.hideLoading();
			}
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		textClick(item){
			this.titile = item.news_title;
			this.content = item.news_detail;
			uni.showModal({
			    title: this.titile,
			    content: this.content,
				showCancel: false,
				confirmText: '关闭',
				confirmColor: '#8dc63f',
				
			    success: function (res) {
			        if (res.confirm) {
			        } else if (res.cancel) {
			        }
			    }
			});
		},
	},
	computed: {
		...mapState(['token', 'globalUserInfo'])
	},
	data() {
		return {
			swiperList: [],
			newsList: [],
			modalName: null,
		};
	}
}
